'use client';
import Link from 'next/link';
import { useState } from 'react';
import { Menu, Close } from '@mui/icons-material';

const navLinks = [
  { name: 'Platform', href: '/platform' },
  { name: 'Datasets', href: '/datasets' },
  { name: 'Use Cases', href: '/use-cases' },
  { name: 'Docs', href: '/docs' },
  { name: 'Contact', href: '/contact' },
];


const MarketingHeader = () => {
  const [mobileOpen, setMobileOpen] = useState(false);

  return (
    <header className="sticky top-0 z-50 bg-white border-b shadow-sm">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex items-center justify-between h-16">
        {/* Logo */}
        <Link href="/" className="text-xl font-bold tracking-tight text-gray-900">
          HUDLE
        </Link>

        {/* Desktop Nav */}
        <nav className="hidden md:flex space-x-6 items-center">
          {navLinks.map(link => (
            <Link key={link.name} href={link.href} className="text-sm text-gray-700 hover:text-black">
              {link.name}
            </Link>
          ))}
          <Link href="/login" className="text-sm text-gray-700 hover:text-black">Log In</Link>
          <Link
            href="/signup"
            className="ml-4 px-4 py-2 bg-black text-white text-sm rounded-full hover:bg-gray-900 transition"
          >
            Sign Up
          </Link>
        </nav>

        {/* Mobile Nav Toggle */}
        <button className="md:hidden" onClick={() => setMobileOpen(!mobileOpen)}>
          {mobileOpen ? <Close className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
        </button>
      </div>

      {/* Mobile Menu */}
      {mobileOpen && (
        <div className="md:hidden bg-white border-t px-4 py-4 space-y-2">
          {navLinks.map(link => (
            <Link
              key={link.name}
              href={link.href}
              className="block text-sm text-gray-800 hover:text-black"
              onClick={() => setMobileOpen(false)}
            >
              {link.name}
            </Link>
          ))}
          <Link href="/login" className="block text-sm text-gray-800 hover:text-black">Log In</Link>
          <Link
            href="/signup"
            className="block mt-2 w-full text-center bg-black text-white py-2 rounded-full"
          >
            Sign Up
          </Link>
        </div>
      )}
    </header>
  );
}

export default MarketingHeader;
